import React,{useState,useEffect} from 'react';
import {
  Avatar,
  Button,
  CssBaseline,
  TextField,
  Link,
  Grid,
  Typography,
  Container
} from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { makeStyles } from '@material-ui/core/styles';
import { Routes } from '../Routes'
import { Link as RRDLink,useHistory } from 'react-router-dom';
import { observer, inject } from 'mobx-react';
import Alert from '@material-ui/lab/Alert';
const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

const SignIn = observer(({authStore}) => {
  const classes = useStyles();
  const history = useHistory();
  const [error,setError] = useState('')
  const [username,setUsername] = useState('')
  const [password,setPassword] = useState('')
  const [usernameError,setUsernameError] = useState({helperText:null,error:false})
  const [passwordError,setPasswordError] = useState({helperText:null,error:false})
  useEffect(()=>{
    setUsernameError({helperText:null,error:false})
  },[username])
  useEffect(()=>{
    setPasswordError({helperText:null,error:false})
  },[password])
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Войти
        </Typography>
        {error&&<Alert severity="error">{error}</Alert>}
        <form onSubmit={e=>{
          e.preventDefault()
          if(!username){
            setUsernameError({helperText:'имя пользователя не должено быть пустым',error:true})
          }
          if(!password){
            setPasswordError({helperText:'пароль не должен быть пустым',error:true})
          }
          if(!usernameError.error&&!passwordError.error.error&&password&&username){
            authStore.login({username, password}).then(()=>history.push('/')).catch(error => {
        
              if(error.response&&error.response.data&&error.response.data.message){
                setError(error.response.data.message)
              }
          });
          }
        }} className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Username"
            name="username"
            autoComplete="username"
            autoFocus
            value={username}
            onChange={e=>setUsername(e.target.value)}
            {...usernameError}
          />
          <TextField
            variant="outlined"
            margin="normal" 
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            value={password}
            onChange={e=>setPassword(e.target.value)}
            autoComplete="current-password"
            {...passwordError}
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Войти
          </Button>
          <Grid container>
            <Grid item>
              <Link component={RRDLink} to={Routes.SignUp.path} variant="body2">
                {"Нет аккаунта? Зарегистрироваться"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
})


export default inject("authStore")(SignIn);